<template>
  <form @submit.prevent="onSubmit($event)">
    <modal ref="modal" title="Modal title" size="lg" :show.sync="show">
      <div class="row">
        <div class="col-md-12">
          <div class="card-body">
            <form class="form-horizontal" action="" method="post" enctype="multipart/form-data">
              <div class="form-group row">
                <label class="col-md-3 col-form-label">Username</label>
                <div class="col-md-9">
                  <input
                    class="form-control"
                    type="text"
                    v-model="user.username"
                    name="username"
                    placeholder="Enter username"
                  />
                  <span class="help-block">This is a help text</span>
                </div>
              </div>
              <div class="form-group row">
                <label class="col-md-3 col-form-label">Date Input</label>
                <div class="col-md-9">
                  <input class="form-control" v-model="user.date" type="date" name="date-input" placeholder="date" />
                  <span class="help-block">Please enter a valid date</span>
                </div>
              </div>
              <div class="form-group row">
                <label class="col-md-3 col-form-label">Role</label>
                <div class="col-md-9">
                  <input
                    class="form-control"
                    type="text"
                    v-model="user.role"
                    name="username"
                    placeholder="Enter role"
                  />
                  <!--                  <span class="help-block">This is a help text</span>-->
                </div>
              </div>
              <div class="form-group row">
                <label class="col-md-3 col-form-label">Disabled Select</label>
                <div class="col-md-9">
                  <select v-model="user.status" class="form-control">
                    <option
                      :selected="user.status === item"
                      v-for="(item, index) in statusData"
                      :value="item"
                      :key="index"
                    >
                      {{ item }}
                    </option>
                  </select>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </modal>
  </form>
</template>

<script>
import Modal from './Modal'
import moment from 'moment'
import _ from 'lodash'

export default {
  name: 'BasicAddEdit',
  components: { Modal },
  data() {
    return {
      show: false,
      user: {
        username: '',
        date: null,
        role: '',
        status: ''
      },
      statusData: ['Active', 'Banned', 'Pending', 'Inactive']
    }
  },
  props: {
    data: Object
  },
  watch: {
    data: {
      handler() {
        if (this.data.id !== null) {
          this.user = _.cloneDeep(this.data)
          this.user.date = this.getDate
        }
      },
      deep: true
    }
  },
  computed: {
    getDate() {
      return moment(this.user.date).format('YYYY-MM-DD')
    }
  },
  methods: {
    onSubmit(e) {
      this.$parent.updateData(this.user)
      this.closeModal(e)
    },
    closeModal(e) {
      this.$refs.modal.hide(e)
    },
    showModal() {
      this.show = true
    }
  }
}
</script>

<style scoped>
.help-block {
  color: red;
}
</style>
