<script>
import table from '../Table/table.json'
import BasicAddEdit from './BasicAddEdit'
import moment from 'moment'
import _ from 'lodash'
import Pagination from './Pagination'

export default {
  name: 'BasicTemplate',
  components: { Pagination: Pagination, BasicAddEdit },
  data() {
    return {
      tableData: '',
      user: {},
      date: null,
      totalPage: 5,
      currentPage: 1
    }
  },
  mounted() {
    this.tableData = table
  },
  methods: {
    getClass(value) {
      if (this.currentPage === value) {
        return 'active'
      }
      return ''
    },
    showModal(item = {}) {
      this.user = item
      this.$refs.baseAddEdit.showModal()
    },
    updateData(data) {
      data.date = moment(data.date).format('DD/MM/YYYY')
      let index = _.findIndex(this.tableData, (item) => item.id === data.id)
      _.merge(this.tableData[index], data)
    },
    loadData(value) {
      console.log(value)
    }
  }
}
</script>

