<template>
  <form @submit.prevent="onSubmit()">
    <div class="c-app flex-row align-items-center">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-md-5">
            <div class="card mx-4">
              <div class="card-header p-4">
                <h3 class="title">SỞ THÔNG TIN VÀ TRUYỀN THÔNG</h3>
              </div>
              <div class="card-body p-4">
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">
                      <svg class="c-icon">
                        <use xlink:href="@coreui/icons/sprites/free.svg#cil-user"></use>
                      </svg>
                    </span>
                  </div>
                  <input
                    class="form-control"
                    type="text"
                    placeholder="Tên Đăng Nhập"
                    name="username"
                    v-model="username"
                    v-validate="{
                      required: true
                    }"
                  />
                </div>
                <div class="mb-3 error-body">
                  <span v-show="errors.has('username')" class="error"> Vui lòng nhập tên đăng nhập</span>
                </div>
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">
                      <svg class="c-icon">
                        <use xlink:href="@coreui/icons/sprites/free.svg#cil-envelope-open"></use></svg
                    ></span>
                  </div>
                  <input
                    class="form-control"
                    type="text"
                    placeholder="Email"
                    v-model="email"
                    name="email"
                    v-validate="{
                      required: true
                    }"
                  />
                </div>
                <div class="mb-3 error-body">
                  <span v-show="errors.has('email')" class="error"> Vui lòng nhập email</span>
                </div>
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">
                      <svg class="c-icon">
                        <use xlink:href="@coreui/icons/sprites/free.svg#cil-lock-locked"></use></svg
                    ></span>
                  </div>
                  <input
                    class="form-control"
                    type="password"
                    placeholder="Mật Khẩu"
                    name="password"
                    v-model="password"
                    v-validate="{
                      required: true
                    }"
                  />
                </div>
                <div class="mb-3 error-body">
                  <span v-show="errors.has('password')" class="error"> Vui lòng nhập mật khẩu</span>
                </div>
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">
                      <svg class="c-icon">
                        <use xlink:href="@coreui/icons/sprites/free.svg#cil-lock-locked"></use></svg
                    ></span>
                  </div>
                  <input
                    class="form-control"
                    type="password"
                    name="repeatPassword"
                    v-model="repeatPassword"
                    :disabled="password === ''"
                    placeholder="Nhập lại mật khẩu"
                    v-validate="{
                      required: true,
                      verifyPassword: {
                        password: password,
                        repeatPassword: repeatPassword
                      }
                    }"
                  />
                </div>
                <div class="mb-3 error-body">
                  <span v-show="errors.has('repeatPassword')" class="error"> Mật khẩu không khớp</span>
                </div>
                <button class="btn btn-block btn-primary" type="submit">Đăng Ký</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import { Validator } from 'vee-validate'
import axios from 'axios'

Validator.extend('verifyPassword', {
  getMessage: () => 'Mật khẩu không khới',
  validate(name, { password, repeatPassword }) {
    return password === repeatPassword
  }
})

export default {
  name: 'Signup',
  data() {
    return {
      username: '',
      password: '',
      email: '',
      repeatPassword: ''
    }
  },
  methods: {
    onSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          let data = {
            username: this.username,
            password: this.password,
            email: this.email
          }
          let uri = '/auth/sign'
          axios.post(uri, data).then((response) => {
            console.log(response)
          })
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import 'authentication.scss';
</style>
