import axios from 'axios'

const state = {
  user: undefined
}

const mutations = {
  setUser(state, user) {
    state.user = user
  }
}

const getters = {
  user: (state) => {
    return state.user
  }
}

const actions = {
  // this.$store.dispatch('User/loadUser')
  loadUser({ commit, state }, isUpdate = false) {
    console.log(isUpdate)
    if (state.user === undefined) {
      axios.get('/menus').then((response) => {
        commit('setUser', response.data)
      })
    }
  }
}

export default {
  namespaced: true,
  state: state,
  mutations: mutations,
  actions: actions,
  getters: getters
}
