import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Signup from '../views/authentication/Signup'
import Login from '../views/authentication/Login'
import Table from '../views/Home/Table/Table'
import Search from '../views/Home/Search/Search'
import BasicForm from '../views/Home/BasicForm/BasicForm'
import BasicTemplate from '../views/Home/BasicTemplate/BasicTemplate'
import ExampleModal from '../views/Home/BasicTemplate/ExampleModal'
import store from '../store'
import Unauthorized from '../views/authentication/Unauthorized'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    children: [
      {
        path: 'table',
        component: Table,
        meta: {
          title: 'table',
          permission: {
            role: 'admin'
          }
        }
      },
      {
        path: '',
        component: Search,
        meta: {
          title: 'search'
        }
      },
      {
        path: 'form',
        component: BasicForm,
        meta: {
          permission: {
            role: 'admin'
          }
        }
      },
      {
        path: 'basic',
        component: BasicTemplate,
        meta: {
          permission: {
            role: 'admin'
          }
        }
      },
      {
        path: 'modal',
        component: ExampleModal,
        meta: {
          permission: {
            role: 'admin'
          }
        }
      }
    ]
  },
  {
    path: '/signup',
    name: 'Đăng Ký',
    meta: {
      title: 'signup'
    },
    component: Signup
  },
  {
    path: '/login',
    name: 'Đăng Nhập',
    meta: {
      title: 'login'
    },
    component: Login
  },
  {
    path: '/unauthorized',
    name: 'Unauthorized',
    meta: {
      title: 'Unauthorized'
    },
    component: Unauthorized
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return { x: 0, y: 0 }
  },
  transitionOnLoad: false
})

router.beforeEach((to, from, next) => {
  const publicPages = ['/login']
  const authRequired = !publicPages.includes(to.path)
  const loggedIn = localStorage.getItem('token')

  if (authRequired && !loggedIn) {
    return next('/login')
  } else {
    console.log(to)
    let user = store.getters['User/user']
    to.matched.forEach((item) => {
      let role = item.meta.permission?.role
      if (role !== undefined) {
        if (user.roles?.includes(role)) {
          return next()
        } else {
          return next('/unauthorized')
        }
      }
    })
  }
  next()
})

export default router
