<template>
  <li v-if="isShow" :class="className">
    <slot></slot>
  </li>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'IsAuthorized',
  props: {
    role: String,
    className: String
  },
  computed: {
    isShow() {
      return this.user.roles.includes(this.role)
    },
    ...mapGetters({ user: 'User/user' })
  }
}
</script>

<style scoped></style>
